<style scoped>
.action-link {
    cursor: pointer;
}
</style>

<template>
    <div>
        <div class="card card-default">
            <div class="card-header">
                <div style="display: flex; justify-content: space-between; align-items: center;">
                    <span>OAuth Clients</span>

                    <a
                        class="action-link"
                        tabindex="-1"
                        @click="showCreateClientForm"
                    >Create New Client</a>
                </div>
            </div>

            <div class="card-body">
                <!-- Current Clients -->
                <p class="mb-0" v-if="clients.length === 0">您尚未建立任何 OAuth Clients。</p>

                <table class="table table-borderless mb-0" v-if="clients.length > 0">
                    <thead>
                        <tr>
                            <th>Client ID</th>
                            <th>名稱</th>
                            <th>Secret</th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>

                    <tbody>
                        <tr v-for="client in clients" :key="client.id">
                            <!-- ID -->
                            <td style="vertical-align: middle;">{{ client.id }}</td>

                            <!-- Name -->
                            <td style="vertical-align: middle;">{{ client.name }}</td>

                            <!-- Secret -->
                            <td style="vertical-align: middle;">
                                <code>{{ client.secret }}</code>
                            </td>

                            <!-- Edit Button -->
                            <td style="vertical-align: middle;">
                                <a class="action-link" tabindex="-1" @click="edit(client)">編輯</a>
                            </td>

                            <!-- Delete Button -->
                            <td style="vertical-align: middle;">
                                <a class="action-link text-danger" @click="destroy(client)">刪除</a>
                            </td>

                            <!-- Testing Button -->
                            <td style="vertical-align: middle;">
                                <a
                                    class="action-link text-success"
                                    :href="'/testing/oauth/authorized/' + client.id"
                                    target="_blank"
                                >測試</a>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>

        <!-- Create Client Modal -->
        <div class="modal fade" id="modal-create-client" tabindex="-1" role="dialog">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h4 class="modal-title">Create Client</h4>

                        <button
                            type="button"
                            class="close"
                            data-dismiss="modal"
                            aria-hidden="true"
                        >&times;</button>
                    </div>

                    <div class="modal-body">
                        <!-- Form Errors -->
                        <div class="alert alert-danger" v-if="createForm.errors.length > 0">
                            <p class="mb-0">
                                <strong>噢哦！</strong> 發生了一些錯誤！
                            </p>
                            <br />
                            <ul>
                                <li v-for="error in createForm.errors" :key="error">{{ error }}</li>
                            </ul>
                        </div>

                        <!-- Create Client Form -->
                        <form role="form">
                            <!-- Name -->
                            <div class="form-group row">
                                <label class="col-md-3 col-form-label">名稱</label>

                                <div class="col-md-9">
                                    <input
                                        id="create-client-name"
                                        type="text"
                                        class="form-control"
                                        @keyup.enter="store"
                                        v-model="createForm.name"
                                    />

                                    <span class="form-text text-muted">您的使用者會看到並且信任的名稱。</span>
                                </div>
                            </div>

                            <!-- Redirect URL -->
                            <div class="form-group row">
                                <label class="col-md-3 col-form-label">Redirect URL</label>

                                <div class="col-md-9">
                                    <input
                                        type="text"
                                        class="form-control"
                                        name="redirect"
                                        @keyup.enter="store"
                                        v-model="createForm.redirect"
                                    />

                                    <span class="form-text text-muted">您的應用程式通過授權後所轉址的 Callback URL。</span>
                                </div>
                            </div>

                            <!-- Confidential -->
                            <div class="form-group row">
                                <label class="col-md-3 col-form-label">是否加密傳輸</label>

                                <div class="col-md-9">
                                    <div class="checkbox">
                                        <label>
                                            <input
                                                type="checkbox"
                                                v-model="createForm.confidential"
                                            />
                                        </label>
                                    </div>

                                    <span
                                        class="form-text text-muted"
                                    >要求用戶端使用加密傳輸進行身份驗證，加密可以確保使用安全的方式獲取憑證，而不會將其暴露給未經授權的開發者，公開的應用程式（例如桌面應用程式或 JavaScript SPA 應用程式）無法安全地儲存加密。</span>
                                </div>
                            </div>
                        </form>
                    </div>

                    <!-- Modal Actions -->
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
                        <button type="button" class="btn btn-primary" @click="store">新增</button>
                    </div>
                </div>
            </div>
        </div>

        <!-- Edit Client Modal -->
        <div class="modal fade" id="modal-edit-client" tabindex="-1" role="dialog">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h4 class="modal-title">Edit Client</h4>

                        <button
                            type="button"
                            class="close"
                            data-dismiss="modal"
                            aria-hidden="true"
                        >&times;</button>
                    </div>

                    <div class="modal-body">
                        <!-- Form Errors -->
                        <div class="alert alert-danger" v-if="editForm.errors.length > 0">
                            <p class="mb-0">
                                <strong>噢哦！</strong> 發生了一些錯誤！
                            </p>
                            <br />
                            <ul>
                                <li v-for="error in editForm.errors" :key="error">{{ error }}</li>
                            </ul>
                        </div>

                        <!-- Edit Client Form -->
                        <form role="form">
                            <!-- Name -->
                            <div class="form-group row">
                                <label class="col-md-3 col-form-label">名稱</label>

                                <div class="col-md-9">
                                    <input
                                        id="edit-client-name"
                                        type="text"
                                        class="form-control"
                                        @keyup.enter="update"
                                        v-model="editForm.name"
                                    />

                                    <span class="form-text text-muted">您的使用者會看到並且信任的名稱。</span>
                                </div>
                            </div>

                            <!-- Redirect URL -->
                            <div class="form-group row">
                                <label class="col-md-3 col-form-label">Redirect URL</label>

                                <div class="col-md-9">
                                    <input
                                        type="text"
                                        class="form-control"
                                        name="redirect"
                                        @keyup.enter="update"
                                        v-model="editForm.redirect"
                                    />

                                    <span class="form-text text-muted">您的應用程式通過授權後所轉址的 Callback URL。</span>
                                </div>
                            </div>
                        </form>
                    </div>

                    <!-- Modal Actions -->
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
                        <button type="button" class="btn btn-primary" @click="update">儲存更新</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    /*
     * The component's data.
     */
    data() {
        return {
            clients: [],

            createForm: {
                errors: [],
                name: "",
                redirect: "",
                confidential: true
            },

            editForm: {
                errors: [],
                name: "",
                redirect: ""
            }
        };
    },

    /**
     * Prepare the component (Vue 1.x).
     */
    ready() {
        this.prepareComponent();
    },

    /**
     * Prepare the component (Vue 2.x).
     */
    mounted() {
        this.prepareComponent();
    },
    methods: {
        /**
         * Prepare the component.
         */
        prepareComponent() {
            this.getClients();

            $("#modal-create-client").on("shown.bs.modal", () => {
                $("#create-client-name").focus();
            });

            $("#modal-edit-client").on("shown.bs.modal", () => {
                $("#edit-client-name").focus();
            });
        },

        /**
         * Get all of the OAuth clients for the user.
         */
        getClients() {
            axios.get("/oauth/clients").then(response => {
                this.clients = response.data;
            });
        },

        /**
         * Show the form for creating new clients.
         */
        showCreateClientForm() {
            $("#modal-create-client").modal("show");
            $(".modal-backdrop").hide();
        },

        /**
         * Create a new OAuth client for the user.
         */
        store() {
            this.persistClient(
                "post",
                "/oauth/clients",
                this.createForm,
                "#modal-create-client"
            );
        },

        /**
         * Edit the given client.
         */
        edit(client) {
            this.editForm.id = client.id;
            this.editForm.name = client.name;
            this.editForm.redirect = client.redirect;

            $("#modal-edit-client").modal("show");
            $(".modal-backdrop").hide();
        },

        /**
         * Update the client being edited.
         */
        update() {
            this.persistClient(
                "put",
                "/oauth/clients/" + this.editForm.id,
                this.editForm,
                "#modal-edit-client"
            );
        },

        /**
         * Persist the client to storage using the given form.
         */
        persistClient(method, uri, form, modal) {
            form.errors = [];

            axios[method](uri, form)
                .then(response => {
                    this.getClients();

                    form.name = "";
                    form.redirect = "";
                    form.errors = [];

                    $(modal).modal("hide");
                })
                .catch(error => {
                    if (typeof error.response.data === "object") {
                        form.errors = _.flatten(
                            _.toArray(error.response.data.errors)
                        );
                    } else {
                        form.errors = [
                            "Something went wrong. Please try again."
                        ];
                    }
                });
        },

        /**
         * Destroy the given client.
         */
        destroy(client) {
            axios.delete("/oauth/clients/" + client.id).then(response => {
                this.getClients();
            });
        }
    }
};
</script>
